import { WS_CLOSED, WS_ERROR, WS_GET_ORDERS, WS_START, WS_SUCCESS } from '../types/orders';

//     
const initialState = {
  orders: [], //  
  isLoading: false, //   
  isConnection: false, //     WebSocket
  hasConnectionFailed: false, //     WebSocket
  total: 0, //   
  totalToday: 0, //      
  errors: [], //  ,   
};

//     
export const ordersReducer = (state = initialState, { type, payload }) => {
  switch (type) {
    case WS_START:
      //    WebSocket
      return {
        ...initialState,
        isLoading: true,
      };
    case WS_SUCCESS:
      //    WebSocket
      return {
        ...state,
        isConnection: true,
      };
    case WS_ERROR:
      //     WebSocket
      return {
        ...state,
        hasConnectionFailed: true,
        errors: [...state.errors, payload],
      };
    case WS_GET_ORDERS: {
      //      WebSocket
      const { orders, total, totalToday } = payload;
      return {
        ...state,
        isLoading: false,
        hasConnectionFailed: false,
        orders: [...orders],
        total,
        totalToday,
      };
    }
    case WS_CLOSED:
      //  WebSocket
      return {
        ...state,
        isConnection: false,
      };
    default:
      //       
      return state;
  }
};
