//      
import {
  ADD_BUN,
  ADD_INGREDIENT,
  DELETE_ALL_INGREDIENTS,
  DELETE_INGREDIENT,
  SEND_ORDER,
  SEND_ORDER_FAILED,
  SEND_ORDER_SUCCESS,
  SORT_INGREDIENTS,
  SET_ORDER_STATE,
} from '../types/burger';

//     
const initialState = {
  bun: {}, //   
  ingredients: [], //  
  isRequesting: false, //   
  hasRequestFailed: false, //    
  order: {}, //   
  errors: [], //  
  isOrder: false, //   
};

//      
export const burgerReducer = (state = initialState, { type, payload }) => {
  //     
  switch (type) {
    //  
    case ADD_INGREDIENT: {
      return {
        ...state,
        ingredients: [...state.ingredients, payload],
      };
    }
    //  
    case ADD_BUN: {
      return {
        ...state,
        bun: payload,
      };
    }
    //  
    case DELETE_INGREDIENT: {
      if (payload.type === 'bun') {
        return {
          ...state,
          bun: {},
        };
      }

      const updatedIngredients = state.ingredients.filter((ingredient) => ingredient.uuid !== payload);

      return {
        ...state,
        ingredients: updatedIngredients,
      };
    }
    //   
    case DELETE_ALL_INGREDIENTS: {
      return {
        ...state,
        bun: {},
        ingredients: [],
      };
    }
    //  
    case SORT_INGREDIENTS: {
      return {
        ...state,
        ingredients: [...payload],
      };
    }
    //  
    case SEND_ORDER: {
      return {
        ...state,
        isRequesting: true,
        hasRequestFailed: false,
        order: {},
        errors: [],
      };
    }
    //    
    case SEND_ORDER_FAILED: {
      return {
        ...state,
        isRequesting: false,
        hasRequestFailed: true,
        errors: [...state.errors, payload],
      };
    }
    //   
    case SEND_ORDER_SUCCESS: {
      return {
        ...state,
        isRequesting: false,
        order: payload,
      };
    }
    case SET_ORDER_STATE: {
      return {
        ...state,
        isOrder: payload,
      };
    }
    //    ,   
    default: {
      return state;
    }
  }
};
