import cookieManager from '../../utils/cookieManager';
import { TOKEN_NAMES } from '../../utils/constants';

// Middleware    WebSocket
export const socketMiddleware = (wsUrl, wsActions) => {
  return (store) => {
    let socket = null; //      WebSocket

    const { dispatch, getState } = store; //   dispatch  getState  
    const { wsInit, onOpen, onClose, onError, onMessage } = wsActions; //    WebSocket  

    return (next) => (action) => {
      const { type } = action; //     

      //      
      const accessToken = cookieManager.getCookie(TOKEN_NAMES.accessToken);

      //  WebSocket    wsInit
      if (type === wsInit) {
        const token = accessToken?.replace('Bearer ', ''); //     
        socket = new WebSocket(`${wsUrl}?token=${token}`); //   WebSocket     URL
      }

      //  WebSocket    onClose
      if (type === onClose) {
        socket && socket.close(1000, 'CLOSE_NORMAL'); //   WebSocket  
      }

      //   WebSocket,   WebSocket 
      if (socket) {
        //    
        socket.onopen = (event) => dispatch({ type: onOpen, payload: event });

        //    
        socket.onerror = (event) => {
          dispatch({ type: onError, payload: event });
        };

        //    
        socket.onmessage = (event) => {
          const { data } = event;
          const { success, ...restParsedData } = JSON.parse(data); //  JSON-  
          if (!success) dispatch({ type: onError, payload: event });
          dispatch({ type: onMessage, payload: restParsedData });
        };

        //    
        socket.onclose = (event) => dispatch({ type: onClose, payload: event });
      }

      next(action); //    middleware  
    };
  };
};
